.error-state {
  background-color: rgb(189, 0, 0);
  color: white;
  font-weight: bold;
  padding: 0.5rem;
  border-radius: 0.5rem;
  margin: 0.5rem;
  text-align: center;
  border: 2px solid red;
  height: 60%;
  width: 98%;
  position: relative;
  top: -80px;
  margin: auto;
}
.error-span {
  display: block;
  padding: 0.5rem;
  font-size: 1rem;
  color: red;
  font-weight: bold;
  background-color: white;
  margin-top: 50px;
}
.admin {
  text-align: center;
  font-weight: bold;
  color: green;
  background-color: rgb(237, 237, 102);
}

.prod-header {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  align-items: center;
  padding: 0 10px 0px 0px;
  box-sizing: border-box;
  border: 1px solid black;
  width: 100%;
  height: 100%;
  margin: 0px auto;
  padding: 0 0rem;
  background-color: beige;
}

.grid-1 {
  grid-column-start: 1;
  font-weight: bold;
}
